import React from 'react';
import { Link } from 'react-router-dom';

// Components

const ThankYou = (props) => {
  return (
    <div>
      <p>
        Merci pour votre message !<br />
        Vous ne m'avez pas tout dit ? Cliquez ci-dessous pour m'écrire à nouveau...
      </p>
      <Link className="btn btn--rounded btn--shadow" to="/contact">nouveau message</Link>
    </div>
  );
};

export default ThankYou;


