import React from 'react';
import { NavLink } from 'react-router-dom';
import classNames from 'classnames';

const Nav = (props) => {
  let navClass = classNames({
    'nav': true,
    'nav--open': props.isOpen,
  });

  return (
    <nav className={ navClass }>
      <ul className="nav__list">
        <li className="nav__item">
          <NavLink
            exact
            to="/"
            className="nav__link"
            onClick={ props.onClickMenuItem }
            activeClassName="nav__link nav__link--active">Accueil</NavLink>
        </li>
        <li className="nav__item">
          <NavLink
            exact
            to="/about"
            className="nav__link"
            onClick={ props.onClickMenuItem }
            activeClassName="nav__link nav__link--active">À propros</NavLink>
        </li>
        <li className="nav__item">
          <NavLink
            exact
            to="/skills"
            className="nav__link"
            onClick={ props.onClickMenuItem }
            activeClassName="nav__link nav__link--active">Compétences</NavLink>
        </li>
        <li className="nav__item">
          <NavLink
            exact
            to="/laboratory"
            className="nav__link"
            onClick={ props.onClickMenuItem }
            activeClassName="nav__link nav__link--active">Laboratoire</NavLink>
        </li>
        <li className="nav__item">
          <NavLink
            exact
            to="/contact"
            className="nav__link"
            onClick={ props.onClickMenuItem }
            activeClassName="nav__link nav__link--active">Contact</NavLink>
        </li>
      </ul>
    </nav>
  );
};

export default Nav;


