import React, { Component } from 'react';

// Components

class ContactForm extends Component {
  resizeTextarea() {

  }

  handleChange(e) {
    console.log(this.textarea.value, this.textarea.value.length);
  }

  render() {
    return (
      <form className="contact-form" action="//formspree.io/thomas@vandenhede.me" method="POST">
        <h3>Vous souhaitez m'écrire&nbsp;? J'ai tout prévu.</h3>
        <p>
          Vous vous appelez <input
            ref={ el => this.nameInput = el } className="contact-form__input"
            type="text"
            name="name"
            placeholder="Jean Dupont"
            required />, votre adresse de retour est <input ref={ el => this.nameInput = el } className="contact-form__input" type="email" name="_replyto" placeholder="jeandupont@exemple.fr" required />, et vous m'écrivez à propos de<input ref={ el => this.subjectInput = el } className="contact-form__input" name="_subject" placeholder="quelque chose" required/> .
        </p>
        <p>
          C'est à vous de jouer :<br />
          <textarea
            className="contact-form__textarea"
            name="message"
            rows="2"
            ref={ el => this.textarea = el }
            placeholder="Votre superbe message ici"
            onChange={ e => this.handleChange(e) }
            required
          ></textarea>
          <input className="hidden" type="text" name="_gotcha" style={{'display':'none'}} />
          <input className="hidden" type="hidden" name="_subject" value="Message via http://domain.com" />
          <input type="hidden" name="_next" value="/contact/thank-you" />
        </p>
        <p>
          <input className="btn btn--rounded btn--shadow submit" type="submit" value="Envoyer" />
        </p>
      </form>
    );
  }
};

export default ContactForm;


