import React from 'react';

// Components
import SimpleSlider from './SimpleSlider';

const LaboratorySection = (props) => {
  return (
    <main className="main-content">
      <section id="laboratory" className="section laboratory">
        <div className="wrapper wrapper--center">
          <h2>
            Mon Laboratoire
          </h2>
          <hr />
          <p>
            Jetez un coup d'oeil à quelques-unes de mes expérimentations...
          </p>
          <p>
            Vous pouvez retrouver toutes mes créations personnelles sur mes pages <a href="https://github.com/ThomasVandenhede/" target="_blank" rel="noopener noreferrer">Github</a> et <a href="https://codepen.io/ThomasVandenhede/" target="_blank" rel="noopener noreferrer">Codepen</a>.
          </p>
          <div className="wrapper">
            <SimpleSlider />
          </div>
        </div>
      </section>
    </main>
  );
};

export default LaboratorySection;


